<template>

  <div>
    <h3>设备信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="12" :lg="12">
        <Form :model="formData" :label-width="100">
          <FormItem label="设备编号">
            <Input v-model="formData.equipment_no" placeholder="请填写设备编号"></Input>
          </FormItem>
          <FormItem label="设备名称">
            <Input v-model="formData.name" placeholder="请填写设备名称"></Input>
          </FormItem>
          <FormItem label="所属空间">
            <Select v-model="formData.space_id">
              <Option v-for="(item,index) in space" v-bind:key="index" :label="item.name"
                      :value="item.id"></Option>
            </Select>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="formData.status">
              <Option label="不可用" :value="0"></Option>
              <Option label="可用" :value="1"></Option>
            </Select>
          </FormItem>
          <FormItem label="设备类型">
            <Select v-model="formData.type_id">
              <Option v-for="(item,index) in type" v-bind:key="index" :label="item.name"
                      :value="item.id"></Option>
            </Select>
          </FormItem>

          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>

        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'
import typeApi from './type/api'
import spaceApi from '../space/api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {},
      space: [],
      type: [],
      load: false,
      times: []
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
    this.loadSpace()
    this.loadType()
  },
  methods: {
    loadType () {
      typeApi.select(res => {
        this.type = res.data.data
      })
    },
    loadSpace () {
      spaceApi.select(res => {
        this.space = res.data.data
      })
    },
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data
        this.formData.space_id = this.formData.space.id
      }, () => {
        this.load = false
      })
    },
    submit () {
      this.load = true
      api.save(this.formData, () => {
        this.load = false
        this.$router.push({ name: 'equipment' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

